@import url(https://fonts.googleapis.com/css?family=Proza+Libre);
@import url(https://js.arcgis.com/4.9/esri/css/main.css);
body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

input[type="file"].uploadImageInput {
  display: none; }

.imageComponentButtonText {
  margin-right: 10px; }

.selectedImagePreview {
  margin-top: 10px;
  max-height: 70vh;
  max-width: 70vh; }

.uploadSidewalkImageText {
  padding-left: 10px; }

.infiniteImageListRow {
  display: flex;
  flex-direction: row;
  align-items: center;
  padding: 0 10px; }

.infiniteImageRowSelected {
  width: 100%;
  height: 90%; }

.infiniteImageRowUnselected {
  width: 80%;
  height: 80%;
  margin: auto; }

.backgroundImage {
  max-width: 70vw;
  max-height: 100vh; }

.labelledImageContainer {
  position: relative;
  text-align: center;
  color: white; }

.selectedImageWrapper {
  display: flex;
  margin: auto;
  align-items: center;
  justify-content: center;
  width: calc(100vw - 250px); }

.previewSidewalkImages {
  width: 35vw;
  max-width: 200px;
  margin-bottom: 10px; }

.fillAvailable {
  height: -webkit-fill-available;
  height: -moz-available;
  height: fill-available; }

#mapContainer {
  min-height: 91vh;
  cursor: pointer; }

.sidewalkDrawer {
  width: 50vw;
  max-width: 600px; }
  .sidewalkDrawer .drawerImageSection {
    width: 40vw;
    max-width: 400px; }
  .sidewalkDrawer .streetNameSection {
    font-size: 24px;
    text-align: center; }
  .sidewalkDrawer .comments {
    width: 100%; }
    .sidewalkDrawer .comments .commentBox {
      width: 95%; }
    .sidewalkDrawer .comments .commentDisplaySection {
      border-width: 1px;
      border-style: solid;
      border-color: #a7a7a7;
      border-radius: 15px;
      width: 95%;
      text-align: left; }
    .sidewalkDrawer .comments .commentDisplayBox {
      width: auto;
      padding: 5px 5px 5px 5px;
      margin: 10px 10px 10px 10px;
      text-align: left;
      overflow-wrap: break-word; }

.alertIcon {
  font-size: 20; }

.errorAlert {
  background-color: #d32f2f !important;
  margin: 10px 10px 10px 10px; }

.successAlert {
  background-color: #018701 !important;
  margin: 10px 10px 10px 10px; }

.alertMessage {
  margin-left: 5px; }

.alertBody {
  display: flex;
  align-items: center;
  font-size: 14px; }

.credentialErrorDisplay {
  color: #cc1212; }

.loginContainer {
  border-style: solid;
  border-color: #cabfbf;
  border-width: 1px;
  box-shadow: 5px 6px 8px 4px #bbafaf;
  margin-left: 33%;
  margin-top: 10%;
  margin-right: 35%;
  padding: 20px;
  padding-bottom: 80px;
  border-radius: 5px; }

.adminTitleLogin {
  color: #3e89a7; }

.buttonContainer {
  min-height: 50px;
  max-height: 100px;
  max-width: 300px;
  z-index: 1201;
  position: absolute;
  bottom: 0; }

.btn-success {
  position: absolute !important;
  z-index: 1205 !important;
  margin-left: 100px; }

.btn-danger {
  position: absolute !important;
  z-index: 1205 !important; }

/* bootstrap button overrides */
.btn-primary {
  background-color: #9C27B0 !important; }

.btn-primary:hover, .btn-primary:focus {
  background-color: #6A1B9A !important; }

/* default font */
body {
  font-family: "Proza Libre", sans-serif; }

.row {
  display: flex;
  justify-content: space-between;
  width: 75%;
  align-items: center; }
  .row :nth-child(1) {
    display: block;
    margin-right: 5px;
    margin-left: 14px; }

.padding25 {
  padding: 25px 25px 25px 25px; }

.marginUpDown15 {
  margin-top: 15px;
  margin-bottom: 15px; }

.marginTop10 {
  margin-top: 10px; }

.marginRight10 {
  margin-right: 10px; }

.closeButton {
  float: right;
  cursor: pointer;
  margin-left: auto;
  margin-right: 0; }

.closeButton:hover {
  color: red; }

.clickableItem {
  cursor: pointer; }

div.noOutlineDiv:focus {
  outline: none; }

.wrapText {
  word-break: break-word; }

